/*
 *   Pagination
 */

$class: ".gov-breadcrumbs";

#{$class} {
	@include base(flex);
	justify-content: space-between;
	align-items: flex-start;
	gap: $breadcrumbs-gap;

	nav {
		overflow: hidden;
		width: 100%;
	}
	ul {
		@extend %reset-ul;
		@include base(flex);
		flex-direction: column;
		gap: 8px;
		margin-bottom: 0;
	}
	li {
		@extend %reset-ul-li;
		display: flex;
		justify-content: flex-start;
		align-items: flex-start;
		margin-bottom: 0 !important;
		.gov-icon {
			flex: 0 0 auto;
			width: $breadcrumbs-icon-size;
			height: $breadcrumbs-icon-size;
			margin: calc((24px - $breadcrumbs-icon-size) / 2) $breadcrumbs-gap 0;
			color: $breadcrumbs-arrow-color;
		}
		> a:first-child,
		> button:first-child {
			margin-left: calc($breadcrumbs-icon-size + (2 * $breadcrumbs-gap));
		}
	}
	a {
		@include typography-component($font-size-m);
		color: $breadcrumbs-link-color;
	}
	button {
		@include typography-component($font-size-m);
		color: $breadcrumbs-link-color;
		padding: 0;
		border: none;
		background: none;
		text-decoration: underline;
	}

	@include parameter("collapsed") {
		align-items: center;
		ul {
			overflow-x: hidden;
			flex-direction: row;
		}
		li {
			align-items: center;
			white-space: nowrap;
			.gov-icon {
				margin-top: 0;
			}
		}
		li > a:first-child,
		li > button:first-child {
			margin-left: 0;
		}
	}
}
